/**
 * YouTube URLÏóêÏÑú video IDÎ•º Ï∂îÏ∂úÌïòÎäî Ìï®Ïàò
 * ÏßÄÏõê ÌòïÏãù:
 * - https://www.youtube.com/watch?v=nKpZFe-fx2Q
 * - https://youtu.be/nKpZFe-fx2Q
 * - https://www.youtube.com/embed/nKpZFe-fx2Q
 */
export function getYouTubeVideoId(url: string): string | null {
  if (!url) return null

  try {
    const urlObj = new URL(url)
    const hostname = urlObj.hostname.toLowerCase()

    // YouTube ÎèÑÎ©îÏù∏ Ï≤¥ÌÅ¨
    if (!hostname.includes('youtube.com') && !hostname.includes('youtu.be')) {
      return null
    }

    // 1. watch?v= ÌòïÏãù: https://www.youtube.com/watch?v=nKpZFe-fx2Q
    if (urlObj.searchParams.has('v')) {
      return urlObj.searchParams.get('v')
    }

    // 2. youtu.be ÌòïÏãù: https://youtu.be/nKpZFe-fx2Q
    if (hostname.includes('youtu.be')) {
      const pathname = urlObj.pathname.replace(/^\//, '')
      return pathname || null
    }

    // 3. embed ÌòïÏãù: https://www.youtube.com/embed/nKpZFe-fx2Q
    const embedMatch = urlObj.pathname.match(/\/embed\/([^/?]+)/)
    if (embedMatch) {
      return embedMatch[1]
    }

    return null
  } catch {
    // URL ÌååÏã± Ïã§Ìå® Ïãú Ï†ïÍ∑úÏãùÏúºÎ°ú ÏßÅÏ†ë Ï∂îÏ∂ú
    // watch?v= ÌòïÏãù
    const watchMatch = url.match(/[?&]v=([^&]+)/)
    if (watchMatch) return watchMatch[1]

    // youtu.be ÌòïÏãù
    const shortMatch = url.match(/youtu\.be\/([^/?]+)/)
    if (shortMatch) return shortMatch[1]

    // embed ÌòïÏãù
    const embedMatch = url.match(/\/embed\/([^/?]+)/)
    if (embedMatch) return embedMatch[1]

    return null
  }
}

/**
 * URLÏóêÏÑú videoIDÎ•º Ï∂îÏ∂úÌïòÎäî Ìï®Ïàò
 * Ïòà: ?videoID=123 ÎòêÎäî ?videoId=123
 * ÎòêÎäî YouTube URLÏóêÏÑú v ÌååÎùºÎØ∏ÌÑ∞ Ï∂îÏ∂ú
 */
export function getVideoIdFromUrl(
  searchParams: URLSearchParams,
  currentUrl?: string
): string | null {
  // 1. ÏßÅÏ†ë videoID ÌååÎùºÎØ∏ÌÑ∞ ÌôïÏù∏
  const directVideoId = searchParams.get('videoID') || searchParams.get('videoId') || null
  if (directVideoId) return directVideoId

  // 2. ÌòÑÏû¨ URLÏù¥ YouTube URLÏù∏ Í≤ΩÏö∞
  if (currentUrl) {
    const youtubeId = getYouTubeVideoId(currentUrl)
    if (youtubeId) return youtubeId
  }

  return null
}

/**
 * Referer URLÏóêÏÑú videoIDÎ•º Ï∂îÏ∂úÌïòÎäî Ìï®Ïàò
 * YouTube URLÎèÑ ÏßÄÏõê
 */
export function getVideoIdFromReferer(referer: string | null): string | null {
  if (!referer) return null

  // 1. YouTube URLÏóêÏÑú Ï∂îÏ∂ú ÏãúÎèÑ
  const youtubeId = getYouTubeVideoId(referer)
  if (youtubeId) return youtubeId

  // 2. ÏùºÎ∞ò videoID ÌååÎùºÎØ∏ÌÑ∞ ÌôïÏù∏
  try {
    const url = new URL(referer)
    return url.searchParams.get('videoID') || url.searchParams.get('videoId') || null
  } catch {
    // URL ÌååÏã± Ïã§Ìå® Ïãú referer Î¨∏ÏûêÏó¥ÏóêÏÑú ÏßÅÏ†ë Ï∂îÏ∂ú ÏãúÎèÑ
    const match = referer.match(/[?&]videoID=([^&]+)|[?&]videoId=([^&]+)/i)
    return match ? (match[1] || match[2]) : null
  }
}

/**
 * Î™®Îì† ÏÜåÏä§ÏóêÏÑú videoIDÎ•º Ï∂îÏ∂úÌïòÎäî ÌÜµÌï© Ìï®Ïàò
 * Ïö∞ÏÑ†ÏàúÏúÑ: ÌòÑÏû¨ URL > Referer
 */
export function extractVideoId(
  searchParams: URLSearchParams,
  currentUrl?: string | null,
  referer?: string | null
): {
  videoId: string | null
  source: 'url' | 'referer' | null
} {
  // 1. ÌòÑÏû¨ URLÏóêÏÑú videoID Ï∂îÏ∂ú (YouTube URL Ìè¨Ìï®)
  const urlVideoId = getVideoIdFromUrl(searchParams, currentUrl || undefined)

  // 2. RefererÏóêÏÑú videoID Ï∂îÏ∂ú (YouTube URL Ìè¨Ìï®)
  const refererVideoId = getVideoIdFromReferer(referer || null)

  // 3. Ïö∞ÏÑ†ÏàúÏúÑ: ÌòÑÏû¨ URL > Referer
  if (urlVideoId) {
    return { videoId: urlVideoId, source: 'url' }
  }

  if (refererVideoId) {
    return { videoId: refererVideoId, source: 'referer' }
  }

  return { videoId: null, source: null }
}

